<template>
<div>
   <section class="head">
      <div> <a @click="goBack" class="iconfont back"> &#xe614; </a> </div>
      <h1>Reviews List</h1>
      <div class="chartWrap">
       <span class="bag"> <em class="font10"> {{indexData.shoppingCartNum}} </em> </span>
        <a href="javascript:void(0)" @click="$router.push('/cart')" class="iconfont cart"> &#xe669; </a></div>
    </section>
    <div class="scoreWrap">
        <div class="score">
            <div class="score_left">
                <span class="num">{{commentList.spuCommentHeadVo.avgPoints}}/5</span>
                <van-rate v-model="commentList.spuCommentHeadVo.avgPoints" :size="18" color="#f44" void-icon="star" void-color="#eee" readonly/>
                <span class="count">{{commentList&&commentList.spuCommentHeadVo.commentSum}} Reviews</span>
            </div>
            <div class="score_right">
                <div class="starRating">
                    <span class="starClass">5</span>
                    <van-progress :percentage="87" :show-pivot="false" color="#e33946"/>
                    <span class="starRatingCount">995</span>
                </div>
                <div class="starRating">
                    <span class="starClass">5</span>
                    <van-progress :percentage="95" :show-pivot="false" color="#e33946"/>
                    <span class="starRatingCount">995</span>
                </div>
                <div class="starRating">
                    <span class="starClass">5</span>
                    <van-progress :percentage="80" :show-pivot="false" color="#e33946"/>
                    <span class="starRatingCount">995</span>
                </div>
                <div class="starRating">
                    <span class="starClass">5</span>
                    <van-progress :percentage="50" :show-pivot="false" color="#e33946"/>
                    <span class="starRatingCount">995</span>
                </div>
                <div class="starRating">
                    <span class="starClass">5</span>
                    <van-progress :percentage="2" :show-pivot="false" color="#e33946"/>
                    <span class="starRatingCount">995</span>
                </div>
                
            </div>
        </div>
    </div>
    <div class="commentWrap">
        <div class="comment">
            <!-- <div class="setmeal red">
                Lable 1
            </div> -->
                <div :class="{'setmeal':true,'red':labelIndex==index}" v-for='(item,index) in commentList.spuCommentHeadVo.labelList' :key="index" @click="chooseLabel(item,index)">
               {{item.name}}
            </div>
        </div>
    </div>
    <div class="evaluate">
        <ul>
            <li class="" v-for="(item,index) in commentList.spuCommentFootVos" :key="index">
                <van-cell :value="item.commentTimeStr">
                    <template slot="title">
                        <span class="custom-text">{{item.nickName}}</span>
                        <van-rate v-model="item.points" :size="15" color="#f44" void-icon="star" void-color="#eee" readonly/>
                    </template>
                </van-cell>
                <span class="speak">{{item.comment}}</span>
            </li>
        
            
        </ul>
        
    </div>
    <img class="addReviews" src="../common/images/add.png" @click='redirect("/reviews")'>
  </div>
</template>

<script>
import {getCommentList} from '@/api/product.js';
import {mapGetters} from 'vuex'
export default {
  name: 'reviewsList',
  data () {
    return {
     labelIndex:-1,
     spuCode:this.$route.query.spuCode,
     commentList:[]
    }
  },
  computed: {
      ...mapGetters(['indexData'])
  },
  mounted() {
    this.init();
  },
  methods: {
    init(){
       getCommentList({spuCode:this.spuCode}).then(res=>{
           this.commentList=res.data.result;
       }) 
    },
    changeSort(val){
      this.sortValue=val;
      this.sortVisible=false;
    },
    goBack(){
      this.$router.back(-1)
    },
    chooseLabel(label,index){
        this.labelIndex=index;
         getCommentList({spuCode:this.spuCode,labelId:label.id}).then(res=>{
           this.commentList=res.data.result;
       }) 
    }


  }
}
</script>

<style lang="scss" scoped>
   .scoreWrap{
       width: 100%;
       height: 134px;
       padding-top: 10px;
        background:rgba(246,246,246,1);
   }
   .score{
       width: 100%;
       padding: 0 4%;
       background-color:  white;
       height: 100%;
       display: -webkit-box;
       .score_right{
            .starRating{
                display: flex; align-items: center; justify-content: center;
                padding-top: 7px;
                .van-progress{
                    height: 6px;
                    width: 149px;
                    margin: 0 13px 0 5px;
                    color: #e33946;
                }
                .starClass{
                    display: block;
                    color: #666666;
                }
                .starRatingCount{
                    display: block;
                    color: #999999;
                }
            }
       }
   }
   .score_left{
       height: 100%;
       width: 35%;
   }
   .num{
       font-size: 5vw;
       color: #E33946;
       padding-left: 5px;
       display: block;
       width: 100%;
       font-size: 20px;
       font-weight: 500;
       padding: 15px 0 9px 5px;
   }
   .count{
        color:rgba(102,102,102,1);
        padding-left: 5px;
        font-size: 12px;
        padding-top: 9px;
   }
   .score_right{
       height: 100%;
       width: 65%;
   }
   .commentWrap{
       padding-top: 10px;
       background:rgba(246,246,246,1);
   }
   .comment{
      width: 100%;
      padding: 10px 4% 0 4%;
      height: 97px;
      background-color: white;
    }
     .setmeal{
        width: 91px;
        height: 27px;
        font-size: 14px;
        line-height: 27px;
        border: #666666 1px solid;
        border-radius: 2px;
        text-align: center;
        color: #666666;
        float: left;
        margin-top: 10px;
        margin-right: 24px;
    }
    .red{
      border: #E33946 1px solid;
      color: #E33946;
    }
    .evaluate{
         background:rgba(246,246,246,1);
         padding-top: 10px;
    }
    ul{
        background-color: white;
    }
    li .van-cell__title{
        display: -webkit-box;
    }
    li .van-cell{
        padding: 10px 0 0 0 ;
    }
    li .van-cell:not(:last-child)::after{
        border-bottom: none;
    }
    li{
        padding: 0 4%;
        
    }
    .speak{
        width: 100%;
        border-bottom: 1px solid #FFF6F6F6;
        display: block;
        padding: 10px 0;
    }
    .addReviews{
        position: fixed;
        right: 15px;
        bottom: 20px;
        width: 40px;
    }
</style>



